$focused-color: rgb(39 39 234);
$error-color: rgb(186 0 0);

#pp-stripe-card-element {
	display: flex;
	flex-direction: column;
	width: 100%;
	min-width: 250px;
	padding: 0;
	margin: 0;
	background-color: white;
	border-radius: 6px;
	border: 1px solid #d9d9d9;
	overflow: hidden;

	&.focused {
		border-color: transparent;
		outline: 2px solid $focused-color;
	}

	&.focused.error {
		border-color: transparent;
		outline: 2px solid $error-color;
	}

	.card-line {
		display: flex;
		height: 48px;
		width: 100%;
		margin: 0 0 -1px;
		padding: 0 16px;
		align-items: center;
		overflow: hidden;
		border-bottom: 1px solid #d9d9d9;

		.card-line-half {
			display: inline-block;
			width: calc(50% - 2px);
			margin: 0;
			padding: 0;
		}

		.card-line-brand {
			display: flex;
			width: 36px;
			margin: 0 16px 0 0;
			padding: 0;
			align-content: center;
			justify-content: center;

			img {
				max-height: unset !important;
				width: 36px !important;
				margin: 0 !important;
				padding: 0 !important;
				float: unset !important;
			}
		}

		.card-line-number {
			display: inline-block;
			width: calc(100% - 56px);
			margin: 0;
			padding: 0;
		}
	}

	#pp-stripe-card-number,
	#pp-stripe-card-expiry,
	#pp-stripe-card-cvc {
		width: 100%;
		height: 16px;
	}
}

#pp-stripe-card-status {
	display: flex;
	visibility: hidden;
	min-height: 25px;
	width: 100%;
	margin: 0.5rem 0 0;
	padding: 0;
	font-size: 16px;
	align-content: center;
	justify-content: center;

	&.show {
		visibility: visible;
	}

	svg {
		display: flex;
		height: 16px !important;
		width: 16px !important;
		max-height: unset !important;
		min-height: unset !important;
		margin: 0 0.2rem 0 0 !important;
		padding: 0 !important;
		float: unset !important;
		align-self: center;
		fill: #6d6d6d;
	}

	span {
		display: flex;
		margin: 0;
		padding: 0;
		align-self: center;
		color: #6d6d6d;
	}

	&.error {
		span {
			color: $error-color;
		}

		svg {
			fill: $error-color;
		}
	}
}

.pp-recalculate-blur {
	transition: all 0.2s;
}

.loading-blur {
	color: transparent !important;
	animation-duration: 1.25s;
	animation-fill-mode: forwards;
	animation-iteration-count: infinite;
	animation-name: pp-placeholder-shimmer;
	animation-timing-function: linear;
	background: darkgray;
	background: linear-gradient(to right, #eee 10%, #ddd 18%, #eee 33%);
	background-size: 800px 104px;
	height: 100%;
	width: 100%;
	position: relative;
}

@keyframes pp-placeholder-shimmer {
	0% {
		background-position: -468px 0;
	}

	100% {
		background-position: 468px 0;
	}
}
