// stylelint-disable-next-line selector-class-pattern
.payment_method_peachpay_purchase_order {
	input[type='text'] {
		width: 100%;
		border-radius: 6px;
		color: #000;
		box-shadow: none;
		background-color: #fff;
		border: 1px solid #d9d9d9;
		outline-width: 2px;
		outline-color: #006aff;
	}

	input[type='text']:focus {
		background-color: #fff;
	}

	// Forcefully keep the placeholder text from fading out
	input[type='text']::placeholder {
		opacity: 1;
	}
}
