@import '../../@shared/tokens/colors';
@import '../../@shared/scss/variables';
@import './stripe';
@import './purchase-order';

.peachpay-testmode {
	background-color: $orchColorOrange800;
	color: white;
	padding: 0.2rem;
	margin-bottom: 1rem;
	text-align: center;
	font-size: smaller;
}

.peachpay-tokenize-error {
	background-color: red;
	color: white;
	padding: 0.2rem;
	margin-bottom: 1rem;
	text-align: center;
	font-size: smaller;
}

span.peachpay-gateway-icons {
	float: right;

	img {
		margin-left: 0.2rem;
		max-height: 30px !important;
	}
}

label.radio.peachpay-radio-label {
	display: inline-block !important;
	margin-right: 15px !important;
}

.peachpay-native-powered-by-notice {
	display: flex;
	align-items: center;
	gap: 5px;
	font-size: 12px;

	a {
		display: flex;
		align-items: center;
	}

	svg {
		width: fit-content;
		height: 14px;
	}
}

.hide {
	display: none !important;
}
